.stage {
  @include section();

  background-color: $primaryColor;

  &__wrapper {
    display: flex;
    flex-direction: column-reverse;

    @media ($tablet) {
      flex-direction: row;
      justify-content: space-between;
    }
  }

  &__title {
    @include h1();
  }

  &__list {
    @include lead();


    &-item {
      list-style: url(../images/check-icon.png);
      padding-left: 0.5rem;
    }
  }

  &__image {
    @include img($white);
    width: 100%;
    height: 21.4rem;
    margin-bottom: 2rem;

    @media ($tablet) {
      flex-shrink: 0;
      max-width: 300px;
      margin-bottom: 0;
      margin-left: 4rem;
    }

    @media ($desktop) {
      max-width: 459px;
      margin-bottom: 0;
      margin-left: 8rem;
    }
  }
}